@import "../../App.scss";

.introduction{
    display: flex;
    height: 100vh;
    
}

.intro-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.intro-right{
    flex: 1;
    position: relative;
}

.intro-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.intro-greeting{
    font-size: 30px;
    font-weight: 300;
}

.intro-name{
    font-size: 60px;
}

.intro-tags{
    height: 50px;
    overflow: hidden;
}

.intro-tags-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move{
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
}

.intro-tags-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: $primary-font-color;
    display: flex;
    align-items: center;
}

.intro-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.intro-bg{
    clip-path: polygon(100% 0%, 100% 52%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color:$primary-background-color;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.intro-scroll{
    color:$primary-accent-color
}